@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
:root{--background-color: hsl(261, 100%, 4%);--background-color-inverted: hsl(81, 100%, 96%);--background-color-light: hsl(261, 100%, 7%);--primary-color: hsl(261, 100%, 30%);--primary-color-faded: hsla(261, 100%, 30%, 0.8);--secondary-color: hsl(30, 100%, 47%);--secondary-color-faded: hsla(30, 100%, 47%, 0.08);--secondary-color-inverted: hsl(210, 100%, 53%)}.App{min-height:100vh;background:radial-gradient(farthest-corner at 3rem 3rem, transparent 80%, hsla(30, 100%, 47%, 0.08) 100%),radial-gradient(farthest-side at 2rem 2rem, hsla(30, 100%, 47%, 0.08) 1%, transparent 90%),hsl(261, 100%, 4%);background:radial-gradient(farthest-corner at 3rem 3rem, transparent 80%, var(--secondary-color-faded) 100%),radial-gradient(farthest-side at 2rem 2rem, var(--secondary-color-faded) 1%, transparent 90%),var(--background-color);display:flex;flex-direction:column}.App>h1{position:fixed;padding:1rem;left:0;bottom:0;opacity:0.1}

.Home{display:flex;flex-direction:column}.Home .Modal .container{width:80%;min-width:320px}.Home .LoadingIndicator{margin:auto}.Home.loading-modal .BitCoinRangeForm{display:none}.Home .BitCoinRangeForm{margin:auto}

form{background-color:var(--background-color-light);min-width:20rem;min-height:6.25rem;display:flex;flex-direction:column;padding:1rem 2rem;border:2px solid var(--primary-color);border-radius:2rem}form label{color:var(--secondary-color);margin-left:1rem;margin-bottom:0.2rem}form input{padding:0.2rem 0.5rem}form input:not(:last-of-type){margin-bottom:1rem}form input,form button{background-color:var(--background-color-inverted);filter:invert(1);border-radius:1rem;border:1px solid var(--secondary-color-inverted);color:#000}form button{margin-top:1rem;margin-left:auto;padding:0.3rem 1.5rem}form button:hover{filter:invert(0.95)}

main{flex:1 1}


.Modal{position:fixed;top:0;height:100vh;width:100vw;background-color:rgba(0,0,0,0.4);animation:fade_in 0.5s forwards ease-in-out;z-index:1}.Modal .container{background-color:var(--background-color-light);border-radius:2rem;display:flex;flex-direction:column;color:#FFF;border:2px solid var(--primary-color);position:fixed;max-width:100%;max-height:90%;overflow-y:auto;top:50%;left:50%;animation:slide_in 0.3s forwards ease-in-out;transform:translate(-50%, -200vh);margin:auto}@media (min-width: 720px){.Modal .container{padding:2rem}}.Modal .container>span{margin-bottom:2rem;margin-left:2rem;margin-right:2rem}.Modal .container>h2{margin-left:3rem;margin-right:2rem}.Modal .container::-webkit-scrollbar-thumb{border:0.5rem solid var(--background-color-light)}.Modal .container::-webkit-scrollbar-track{background:var(--background-color-light)}.Modal button{margin:0 auto}@keyframes fade_in{0%{opacity:0}100%{opacity:1}}@keyframes slide_in{0%{transform:translate(-50%, -200vh)}100%{transform:translate(-50%, -50%)}}

.DataChart{margin-bottom:2rem;display:flex;flex-direction:row;background-color:var(--background-color);padding:0.5rem;overflow:auto visible;min-height:326px}@media (min-width: 720px){.DataChart{min-height:36vw}}.DataChart::-webkit-scrollbar{height:1.5rem}.DataChart .volumes-range{margin-left:auto;margin-right:0.5rem}.DataChart .price-range{margin-left:0.5rem;margin-right:auto}.DataChart .volumes-range,.DataChart .price-range{display:flex;flex-direction:column;min-width:60px;margin-block:1rem}.DataChart .volumes-range .high,.DataChart .price-range .high{margin-top:1rem}.DataChart .volumes-range .low,.DataChart .price-range .low{margin-top:auto;margin-bottom:1rem}.DataChart .chart-container{flex:1 1;display:flex;flex-direction:row;flex-wrap:wrap;min-width:500px}.DataChart .chart-container .first{margin-right:auto}.DataChart .chart-container .last{margin-left:auto}.DataChart span{font-size:0.7rem}.DataChart .volumes-label{color:var(--secondary-color)}.DataChart .prices-label{color:red}.DataChart canvas{border:2px solid var(--primary-color);width:100%}

.LoadingIndicator{animation:rotate 1s infinite ease-in-out}@keyframes rotate{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

:root{--background-color: hsl(261, 100%, 4%);--background-color-inverted: hsl(81, 100%, 96%);--background-color-light: hsl(261, 100%, 7%);--primary-color: hsl(261, 100%, 30%);--primary-color-faded: hsla(261, 100%, 30%, 0.8);--secondary-color: hsl(30, 100%, 47%);--secondary-color-faded: hsla(30, 100%, 47%, 0.08);--secondary-color-inverted: hsl(210, 100%, 53%)}body{margin:0}:root{font-size:16px}@media (min-width: 1920px){:root{font-size:.83333vw}}::-webkit-scrollbar{width:1.5rem}::-webkit-scrollbar-track{background:hsl(261, 100%, 4%);background:var(--background-color);border-radius:100vw;margin-block:1rem}::-webkit-scrollbar-thumb{background-color:hsla(261, 100%, 30%, 0.8);background-color:var(--primary-color-faded);border-radius:100vw;border:0.5rem solid hsl(261, 100%, 4%);border:0.5rem solid var(--background-color)}::-webkit-scrollbar-thumb:hover{background-color:hsl(261, 100%, 30%);background-color:var(--primary-color)}*{box-sizing:border-box;line-height:1.5em;letter-spacing:0.05em;word-spacing:0.07em;word-break:break-word;font-family:"Lato", "Arial", "Helvetica", sans-serif}*:focus{outline:none;box-shadow:0 0 7px 0 hsl(210, 100%, 53%);box-shadow:0 0 7px 0 var(--secondary-color-inverted)}button{cursor:pointer;background-color:hsl(261, 100%, 4%);background-color:var(--background-color);border-radius:1rem;border:1px solid hsl(30, 100%, 47%);border:1px solid var(--secondary-color);color:#fff;padding:0.3rem 1.5rem}button:hover{filter:invert(0.05)}p,input,span,label,button{font-size:1.1rem}h1,h2,h3,h4{color:hsl(30, 100%, 47%);color:var(--secondary-color)}

